.checkbox-container {
  display: block;
  position: relative;
  padding-left: 35px;
  padding-top: 2px;
  margin: 5px;
  cursor: pointer;
  font-size: 1em;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.checkbox-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.checkmark {
  border-radius: 5px;
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  border: 2px solid transparent;
}
.checkmark:hover {
  -webkit-transition: 150ms;
  transition: 150ms;
  border: 2px solid #2c526a;
}

/* When the checkbox is checked, add a background */
.checkbox-container input:checked ~ .checkmark {
  background: #2c526a;
}

/* Create the indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.checkbox-container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the indicator */
.checkbox-container .checkmark:after {
  left: 8px;
  top: 6px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

